'use client'

import Link from 'next/link'
import { usePathname } from 'next/navigation'

const navItems = [
  { label: 'HOME', href: '/' },
  { label: 'SELECTED WORKS', href: '/selected-works' },
  { label: 'IN THE STUDIO', href: '/studio' },
  { label: 'TEXTS / PRESS', href: '/texts' },
  { label: 'PRIVATE VIEW', href: '/private' },
]

export default function Navigation() {
  const pathname = usePathname()

  return (
    <nav style={{ position: 'fixed', top: 0, left: 0, right: 0, zIndex: 100, backgroundColor: 'rgba(255,255,255,0.4)', backdropFilter: 'blur(20px)', borderBottom: '1px solid rgba(0,0,0,0.03)' }}>
      <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px 40px' }}>
        <ul style={{ display: 'flex', gap: '40px', listStyle: 'none', margin: 0, padding: 0 }}>
          {navItems.map((item) => (
            <li key={item.href}>
              <Link
                href={item.href}
                style={{
                  fontSize: '9px',
                  letterSpacing: '0.3em',
                  fontWeight: 500,
                  textTransform: 'uppercase',
                  textDecoration: 'none',
                  transition: 'all 0.5s',
                  color: pathname === item.href ? '#000' : 'rgba(0,0,0,0.3)',
                }}
              >
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  )
}
